import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ResetPassPage } from '../reset-pass/reset-pass';
import { RequestProvider } from '../../providers/request/request';
import { HomePage } from '../home/home';


@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  private formSubmitAttempt: boolean;
  private userError: boolean = false;
  formGroup: FormGroup;
  isValidFormSubmitted = null;

  loginForm = {
    email: '',
    username: '',
    password: ''
  }

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public request: RequestProvider) {

    this.formGroup = new FormGroup({
      username: new FormControl('', [
        Validators.required,
        Validators.minLength(10)
      ]),
      password: new FormControl('', [
        Validators.minLength(6),
        Validators.maxLength(20),
        Validators.required
      ])
    });
  }

  onFormSubmit() {
    this.formSubmitAttempt = false;
    this.userError = false;
    if (this.formGroup.invalid) {
      return;
    }
    this.formSubmitAttempt = true;
    
    this.request.checklogin(this.loginForm).subscribe(data => {
      console.log('login success', data);
      this.navCtrl.setRoot(HomePage);
    },
      error => {
        this.userError = true;
        console.log('login error', error);
      })
  }

  isFieldValid(field: string) {
    return (!this.formGroup.get(field).valid && this.formGroup.get(field).touched) ||
      (this.formGroup.get(field).untouched && this.formSubmitAttempt);
  }

  goToResetPass() {
    this.navCtrl.push(ResetPassPage);
  }

}



